.form{
    width: 450px;
        padding: 20px;
        border-radius: 5px;
        box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.22), 0px 2px 2px rgba(0, 0, 0, 0.24),
            0px 2px 1px rgba(0, 0, 0, 0.32);
            padding-left: 140px;

}

label{
    color:aliceblue
}

.input {
    margin-top: 20px;
border: 0;
    outline: 0;
    width: 100%;
    margin-bottom: 30px;
    border-radius: 20px;
    padding: 10px;
background-color: rgb(227, 227, 248);
   
}

.input:focus{
    background-color: rgb(196, 196, 254);
}

@keyframes slideme {
    0% {
        left: -30px;
        margin-left: 0px;
    }

    30% {
        left: 110%;
        margin-left: 80px;
    }

    100% {
        left: 110%;
        margin-left: 80px;
    }
}

.btn {
    width: 159px;
    height: 42px;
    overflow: hidden;
    position: relative;
    background-color: rgb(194, 137, 255);
    border-radius: 10px;
    font-weight: 600;
    margin-left: 130px;

}

.btn:hover {
    background-color: rgb(122, 68, 173);
    color: aliceblue;
}

.btn:after {
    content: "";
    display: block;
    width: 30px;
    height: 200px;
    margin-left: 60px;
    background: rgba(238, 239, 255, 0.817);
    opacity: 0.5;
    position: absolute;
    left: -40px;
    top: -100px;
    z-index: 1;
    transform: rotate(45deg);
    transition: all 0.1s;
    animation-name: slideme;
    animation-duration: 3s;
    animation-delay: 0.05s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
}